<template>
  <div class="result-view">
    <ul class="result-detail">
      <li class="detail taxable-income">应纳税所得额（元）：<span class="number">{{taxableIncome}}</span></li>
      <li class="detail applicable-tax-rate">适用税率%：<span class="number">{{taxrate}}%</span></li>
      <li class="detail tax-payable">应缴税款（元）：<span class="number">{{taxpayable}}</span></li>
      <li class="detail take-home-pay">税后收入（元）：<span class="number">{{aftertaxincome}}</span></li>
      <div class="placeholder"></div>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'ResultView',
  data () {
    return {
      transferof: this.$store.state.transferof
    }
  },
  computed: {
    taxableIncome: function () {
      let result = this.transferof
      if (result === '') {
        return 0
      }
      return result
    },
    taxrate: function () {
      return 20
    },
    taxpayable: function () {
      let result = this.taxableIncome * 0.2
      return result.toFixed(2)
    },
    aftertaxincome: function () {
      let result = this.transferof - this.taxpayable
      return result.toFixed(2)
    }
  }
}
</script>

<style lang="stylus" scoped>
  .result-view
    .result-detail
      margin-top .54rem
      width 5.14rem
      .detail
        color #88EAFF
        font-size .36rem
        margin-bottom 1.04rem
      .placeholder
        height .36rem
        margin-bottom 1.04rem
</style>



