<template>
  <div class="calculator-8">
    <div class="service">
      <span class="input-name">收入金额（元）</span>
      <input type="text" class="input-number" style="line-height:normal" v-model="franchise">
      <img src="../../../assets/images/circle.png" alt="" class="bordercircle">
    </div>
    <div class="placeholder"></div>
    <div class="placeholder"></div>
  </div>
</template>

<script>
export default {
  name: 'Franchise',
  data () {
    return {
    }
  },
  computed: {
    franchise: {
      get () {
        return this.$store.state.franchise
      },
      set (value) {
        this.$store.commit('getFranchise', {
          franchisenum: value
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/form.styl'
    .calculator-8
      .service
        form-style()
        .input-name
          form-text()
          margin-left .4rem
        .bordercircle
          border-circle()
        .input-number
          input-number()
          font-size .36rem
          color #88EAFF
      .placeholder
        width 6.8rem
        height 1.8rem
</style>



