<template>
  <div class="calculator-3">
    <div class="year-end-bonus">
      <span class="input-name">年终奖金（元）</span>
      <input type="text" class="input-number" style="line-height:normal" v-model.number="bnous">
      <img src="../../../assets/images/circle.png" alt="" class="bordercircle">
    </div>
    <div class="placeholder"></div>
    <div class="placeholder"></div>
  </div>
</template>

<script>
export default {
  name: 'YearendBonus',
  data () {
    return {
    }
  },
  computed: {
    bnous: {
      get () {
        return this.$store.state.bnous
      },
      set (value) {
        this.$store.commit('getBnous', {
          bnousnum: value
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/form.styl'
    .calculator-3
      .year-end-bonus
        form-style()
        .input-name
          form-text()
          margin-left .4rem
        .bordercircle
          border-circle()
        .input-number
          input-number()
          font-size .36rem
          color #88EAFF
      .placeholder
        width 6.8rem
        height 1.8rem
</style>



