<template>
  <div class="calculator-6">
    <div class="service">
      <span class="input-name">收入金额（元）</span>
      <input type="text" class="input-number" v-model.number="publicincome">
      <img src="../../../assets/images/circle.png" alt="" class="bordercircle">
    </div>
    <div class="run-time">
      <span class="input-name">经营时间（月）</span>
      <input type="text" class="input-number" v-model.number="month">
      <img src="../../../assets/images/circle.png" alt="" class="bordercircle">
    </div>
    <div class="placeholder"></div>
  </div>
</template>

<script>
export default {
  name: 'PublicInstitution',
  data () {
    return {
    }
  },
  computed: {
    publicincome: {
      get () {
        return this.$store.state.publicincome
      },
      set (value) {
        this.$store.commit('getPublicincome', {
          publicincomenum: value
        })
      }
    },
    month: {
      get () {
        return this.$store.state.month
      },
      set (value) {
        this.$store.commit('getMonth', {
          monthnum: value
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/form.styl'
    .calculator-6
      .service
      .run-time
        form-style()
        .input-name
          form-text()
          margin-left .4rem
        .bordercircle
          border-circle()
        .input-number
          input-number()
          font-size .36rem
          color #88EAFF
      .placeholder
        width 6.8rem
        height 1.8rem
</style>



