<template>
  <div class="result-view">
    <ul class="result-detail">
      <li class="detail">平均每月（元）：<span class="number">{{monthly}}</span></li>
      <li class="detail">适用税率%：<span class="number">{{taxrate}}%</span></li>
      <li class="detail">速算扣除数（元）：<span class="number">{{deduction}}</span></li>
      <li class="detail">应缴税款（元）：<span class="number">{{taxpayable}}</span></li>
      <li class="detail">实发工资（元）：<span class="number">{{takehomepay}}</span></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'ResultView',
  data () {
    return {
      bnous: this.$store.state.bnous
    }
  },
  computed: {
    monthly: function () {
      let result = this.bnous / 12
      return result.toFixed(2)
    },
    taxrate: function () {
      let monthlywage = this.monthly
      if (monthlywage < 3000) {
        return 3
      } else if (monthlywage > 3000 && monthlywage < 12000) {
        return 10
      } else if (monthlywage > 12000 && monthlywage < 25000) {
        return 20
      } else if (monthlywage > 25000 && monthlywage < 35000) {
        return 25
      } else if (monthlywage > 35000 && monthlywage < 55000) {
        return 30
      } else if (monthlywage > 55000 && monthlywage < 80000) {
        return 35
      } else if (monthlywage > 80000) {
        return 45
      } else {
        return 0
      }
    },
    deduction: function () {
      let monthlywage = this.monthly
      if (monthlywage < 3000) {
        return 0
      } else if (monthlywage > 3000 && monthlywage < 12000) {
        return 210
      } else if (monthlywage > 12000 && monthlywage < 25000) {
        return 1410
      } else if (monthlywage > 25000 && monthlywage < 35000) {
        return 2660
      } else if (monthlywage > 35000 && monthlywage < 55000) {
        return 4410
      } else if (monthlywage > 55000 && monthlywage < 80000) {
        return 7160
      } else if (monthlywage > 80000) {
        return 15160
      } else {
        return 0
      }
    },
    taxpayable: function () {
      let taxratepercent = this.taxrate * 0.01
      let result = this.bnous * taxratepercent - this.deduction
      return result
    },
    takehomepay: function () {
      let result = this.bnous - this.taxpayable
      return result
    }
  }
}
</script>

<style lang="stylus" scoped>
  .result-view
    .result-detail
      margin-top .54rem
      width 5.14rem
      .detail
        color #88EAFF
        font-size .36rem
        margin-bottom 1.04rem
</style>



