<template>
  <div class="result-view">
    <ul class="result-detail">
      <li class="detail">减除费用（元）：<span class="number">{{abatment}}</span></li>
      <li class="detail taxable-income">应纳税所得额（元）：<span class="number">{{taxableIncome}}</span></li>
      <li class="detail applicable-tax-rate">适用税率%：<span class="number">{{taxrate}}%</span></li>
      <li class="detail tax-payable">应缴税款（元）：<span class="number">{{taxpayable}}</span></li>
      <li class="detail take-home-pay">税后收入（元）：<span class="number">{{aftertaxincome}}</span></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Remuneration',
  data () {
    return {
      remuneration: this.$store.state.remuneration
    }
  },
  computed: {
    taxableIncome: function () {
      if (this.remuneration >= 800 && this.remuneration <= 4000) {
        let result = this.remuneration - 800
        return result.toFixed(2)
      } else if (this.remuneration > 4000) {
        let result = this.remuneration * 0.8
        return result.toFixed(2)
      } else {
        return 0
      }
    },
    taxrate: function () {
      return 14
    },
    taxpayable: function () {
      let result = this.taxableIncome * 0.14
      return result.toFixed(2)
    },
    aftertaxincome: function () {
      let result = this.remuneration - this.taxpayable
      return result.toFixed(2)
    },
    abatment: function () {
      let result = this.remuneration - this.taxableIncome
      return result.toFixed(2)
    }
  }
}
</script>

<style lang="stylus" scoped>
  .result-view
    .result-detail
      margin-top .54rem
      width 5.14rem
      .detail
        color #88EAFF
        font-size .36rem
        margin-bottom 1.04rem
</style>



