<template>
  <div class="result-view">
    <ul class="result-detail">
      <li class="detail tax-payable">应缴税款（元）：<span class="number">{{taxpayable}}</span></li>
      <li class="detail take-home-pay">税前工资（元）：<span class="number">{{pretax}}</span></li>
      <div class="placeholder"></div>
      <div class="placeholder"></div>
      <div class="placeholder"></div>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'AftertaxR',
  data () {
    return {
      wage2: this.$store.state.wage2,
      insurance2: this.$store.state.insurance2,
      threshold2: this.$store.state.threshold2
    }
  },
  computed: {
    taxableIncome: function () {
      let result = this.wage2 - this.threshold2
      if (result <= 0) {
        return 0
      } else {
        return result
      }
    },
    taxrate: function () {
      if (this.taxableIncome < 2910) {
        return 3
      } else if (this.taxableIncome > 2910 && this.taxableIncome < 11010) {
        return 10
      } else if (this.taxableIncome > 11010 && this.taxableIncome < 21410) {
        return 20
      } else if (this.taxableIncome > 21410 && this.taxableIncome < 28910) {
        return 25
      } else if (this.taxableIncome > 28910 && this.taxableIncome < 42910) {
        return 30
      } else if (this.taxableIncome > 42910 && this.taxableIncome < 59160) {
        return 35
      } else if (this.taxableIncome > 59160) {
        return 45
      } else {
        return 0
      }
    },
    deduction: function () {
      if (this.taxableIncome < 2910) {
        return 0
      } else if (this.taxableIncome > 2910 && this.taxableIncome < 11010) {
        return 210
      } else if (this.taxableIncome > 11010 && this.taxableIncome < 21410) {
        return 1410
      } else if (this.taxableIncome > 21410 && this.taxableIncome < 28910) {
        return 2660
      } else if (this.taxableIncome > 28910 && this.taxableIncome < 42910) {
        return 4410
      } else if (this.taxableIncome > 42910 && this.taxableIncome < 59160) {
        return 7160
      } else if (this.taxableIncome > 59160) {
        return 15160
      } else {
        return 0
      }
    },
    includetaxableIncome: function () {
      let taxratepercent = this.taxrate * 0.01
      let result = (this.wage2 - this.threshold2 - this.deduction) / (1 - taxratepercent)
      return result
    },
    taxpayable: function () {
      let taxratepercent = this.taxrate * 0.01
      let result = this.includetaxableIncome * taxratepercent - this.deduction
      if (result < 0) {
        return 0
      } else {
        return result.toFixed(2)
      }
    },
    pretax: function () {
      let result = this.includetaxableIncome + this.insurance2 + this.threshold2
      if (result < 0) {
        return 0
      } else {
        return result.toFixed(2)
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  .result-view
    .result-detail
      margin-top .54rem
      width 5.14rem
      .detail
        color #88EAFF
        font-size .36rem
        margin-bottom 1.04rem
      .placeholder
        height .36rem
        margin-bottom 1.04rem
</style>



