<template>
  <div class="container">
    <home-header></home-header>
    <home-form @optionItem="showOptionItem"></home-form>
    <component :is="showWhat"></component>
    <begin-computed></begin-computed>
  </div>
</template>

<script>
import HomeHeader from './components/Header.vue'
import HomeCalculator1 from './components/Pretax.vue'
import HomeCalculator2 from './components/Aftertax.vue'
import HomeCalculator3 from './components/Yearendbonus.vue'
import HomeCalculator4 from './components/PersonalService.vue'
import HomeCalculator5 from './components/Business'
import HomeCalculator6 from './components/PublicInstitution.vue'
import HomeCalculator7 from './components/Remuneration.vue'
import HomeCalculator8 from './components/Franchise.vue'
import HomeCalculator9 from './components/LeasedProperty.vue'
import HomeCalculator10 from './components/TransferOfProperty.vue'
import HomeCalculator11 from './components/Interest.vue'
import HomeCalculator12 from './components/ByAccident.vue'
import BeginComputed from './components/Begincomputed.vue'
import HomeForm from './components/Form.vue'
export default {
  name: 'Home',
  components: {
    HomeHeader,
    HomeForm,
    HomeCalculator1,
    HomeCalculator2,
    HomeCalculator3,
    HomeCalculator4,
    HomeCalculator5,
    HomeCalculator6,
    HomeCalculator7,
    HomeCalculator8,
    HomeCalculator9,
    HomeCalculator10,
    HomeCalculator11,
    HomeCalculator12,
    BeginComputed
  },
  data () {
    return {
      showWhat: 'HomeCalculator1'
    }
  },
  computed: {
  },
  methods: {
    showOptionItem: function (data) {
      let optionIndex = data + 1
      this.showWhat = 'HomeCalculator' + optionIndex
      console.log(this.showWhat)
    }
  }
}
</script>

<style lang="stylus" scoped>
  .container
    width 100%
    position relative
    background url(../../assets/images/bg.png) no-repeat center center
    background-size cover
    display flex
    flex-direction column
    align-items center
    justify-content center
    overflow-x hidden
</style>



