<template>
  <div class="result-view">
    <ul class="result-detail">
      <li class="detail">减除费用（元）：<span class="number">{{abatment}}</span></li>
      <li class="detail taxable-income">应纳税所得额（元）：<span class="number">{{taxableIncome}}</span></li>
      <li class="detail applicable-tax-rate">适用税率%：<span class="number">{{taxrate}}%</span></li>
      <li class="detail quick-deduction">速算扣除数（元）：<span class="number">{{deduction}}</span></li>
      <li class="detail tax-payable">应缴税款（元）：<span class="number">{{taxpayable}}</span></li>
      <li class="detail take-home-pay">税后收入（元）：<span class="number">{{aftertaxincome}}</span></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'ResultView',
  data () {
    return {
      publicincome: this.$store.state.publicincome,
      month: this.$store.state.month
    }
  },
  computed: {
    taxableIncome: function () {
      let result = this.publicincome - (3500 * this.month)
      return result.toFixed(2)
    },
    taxrate: function () {
      if (this.taxableIncome < 15000) {
        return 5
      } else if (this.taxableIncome > 15000 && this.taxableIncome < 30000) {
        return 10
      } else if (this.taxableIncome > 30000 && this.taxableIncome < 60000) {
        return 20
      } else if (this.taxableIncome > 60000 && this.taxableIncome < 100000) {
        return 30
      } else if (this.taxableIncome > 100000) {
        return 35
      } else {
        return 0
      }
    },
    deduction: function () {
      if (this.taxableIncome < 15000) {
        return 0
      } else if (this.taxableIncome > 15000 && this.taxableIncome < 30000) {
        return 750
      } else if (this.taxableIncome > 30000 && this.taxableIncome < 60000) {
        return 3750
      } else if (this.taxableIncome > 60000 && this.taxableIncome < 100000) {
        return 9750
      } else if (this.taxableIncome > 100000) {
        return 14750
      } else {
        return 0
      }
    },
    taxpayable: function () {
      let taxratepercent = this.taxrate * 0.01
      let result = this.taxableIncome * taxratepercent - this.deduction
      return result.toFixed(2)
    },
    aftertaxincome: function () {
      let result = this.publicincome - this.taxpayable
      return result.toFixed(2)
    },
    abatment: function () {
      let result = 3500 * this.month
      return result
    }
  }
}
</script>

<style lang="stylus" scoped>
  .result-view
    .result-detail
      margin-top .54rem
      width 5.14rem
      .detail
        color #88EAFF
        font-size .36rem
        margin-bottom 1.04rem
</style>



