<template>
  <div class="result-view">
    <ul class="result-detail">
      <li class="detail taxable-income">应纳税所得额（元）：<span class="number">{{taxableIncome}}</span></li>
      <li class="detail applicable-tax-rate">适用税率%：<span class="number">{{taxrate}}%</span></li>
      <li class="detail quick-deduction">速算扣除数（元）：<span class="number">{{deduction}}</span></li>
      <li class="detail tax-payable">应缴税款（元）：<span class="number">{{taxpayable}}</span></li>
      <li class="detail take-home-pay">实发工资（元）：<span class="number">{{takehomepay}}</span></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'ResultView',
  data () {
    return {
      wage: this.$store.state.wage1,
      insurance: this.$store.state.insurance1,
      threshold: this.$store.state.threshold1
    }
  },
  computed: {
    taxableIncome: function () {
      let result = this.wage - this.insurance - this.threshold
      if (result <= 0) {
        return 0
      } else {
        return result
      }
    },
    taxrate: function () {
      if (this.taxableIncome < 3000) {
        return 3
      } else if (this.taxableIncome > 3000 && this.taxableIncome < 12000) {
        return 10
      } else if (this.taxableIncome > 12000 && this.taxableIncome < 25000) {
        return 20
      } else if (this.taxableIncome > 25000 && this.taxableIncome < 35000) {
        return 25
      } else if (this.taxableIncome > 35000 && this.taxableIncome < 55000) {
        return 30
      } else if (this.taxableIncome > 55000 && this.taxableIncome < 80000) {
        return 35
      } else if (this.taxableIncome > 80000) {
        return 45
      } else {
        return 0
      }
    },
    deduction: function () {
      if (this.taxableIncome < 3000) {
        return 0
      } else if (this.taxableIncome > 3000 && this.taxableIncome < 12000) {
        return 210
      } else if (this.taxableIncome > 12000 && this.taxableIncome < 25000) {
        return 1410
      } else if (this.taxableIncome > 25000 && this.taxableIncome < 35000) {
        return 2660
      } else if (this.taxableIncome > 35000 && this.taxableIncome < 55000) {
        return 4410
      } else if (this.taxableIncome > 55000 && this.taxableIncome < 80000) {
        return 7160
      } else if (this.taxableIncome > 80000) {
        return 15160
      } else {
        return 0
      }
    },
    taxpayable: function () {
      let taxratepercent = this.taxrate * 0.01
      let result = this.taxableIncome * taxratepercent - this.deduction
      return result.toFixed(2)
    },
    takehomepay: function () {
      let result = this.wage - this.insurance - this.taxpayable
      return result.toFixed(2)
    }
  }
}
</script>

<style lang="stylus" scoped>
  .result-view
    .result-detail
      margin-top .54rem
      width 5.14rem
      .detail
        color #88EAFF
        font-size .36rem
        margin-bottom 1.04rem
</style>



