<template>
  <div class="calculator-1">
    <div class="pre-tax">
      <span class="input-name">税前工资（元）</span>
      <input type="text" class="input-number" v-model.number="wage">
      <img src="../../../assets/images/circle.png" alt="" class="bordercircle">
    </div>
    <div class="social-insurance">
      <span class="input-name">各项社会保险（元）</span>
      <input type="text" class="input-number" v-model.number="insurance">
      <img src="../../../assets/images/circle.png" alt="" class="bordercircle">
    </div>
    <div class="threshold">
      <span class="input-name">起征点（元）</span>
      <input type="text" class="input-number" v-model.number="threshold">
      <img src="../../../assets/images/circle.png" alt="" class="bordercircle">
    </div>
  </div>
</template>

<script>
export default {
  name: 'PreTax',
  data () {
    return {
    }
  },
  methods: {
  },
  computed: {
    wage: {
      get () {
        return this.$store.state.wage1
      },
      set (value) {
        this.$store.commit('getWage1', {
          wagenum: value
        })
      }
    },
    insurance: {
      get () {
        return this.$store.state.insurance1
      },
      set (value) {
        this.$store.commit('getInsurance1', {
          insurancenum: value
        })
      }
    },
    threshold: {
      get () {
        return this.$store.state.threshold1
      },
      set (value) {
        this.$store.commit('getThreshold1', {
          thresholdnum: value
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/form.styl'
    .calculator-1
      .pre-tax
      .social-insurance
      .threshold
        form-style()
        .input-name
          form-text()
          margin-left .4rem
        .bordercircle
          border-circle()
        .input-number
          input-number()
          font-size .36rem
          color #88EAFF
      .social-insurance
        .input-number
          width 1.8rem
      .threshold
        .input-number
          width 2.85rem
</style>



