import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    formIndex: 0,
    wage1: '',
    insurance1: '',
    threshold1: 5000,
    wage2: '',
    insurance2: '',
    threshold2: 5000,
    bnous: '',
    income: '',
    business: '',
    publicincome: '',
    month: '',
    remuneration: '',
    franchise: '',
    leased: '',
    transferof: '',
    interest: '',
    byaccident: ''
  },
  mutations: {
    getformIndex (state, payload) {
      state.formIndex = payload.formIndex
    },
    getWage1 (state, payload) {
      state.wage1 = payload.wagenum
    },
    getInsurance1 (state, payload) {
      state.insurance1 = payload.insurancenum
    },
    getThreshold1 (state, payload) {
      state.threshold1 = payload.thresholdnum
    },
    getWage2 (state, payload) {
      state.wage2 = payload.wagenum2
    },
    getInsurance2 (state, payload) {
      state.insurance2 = payload.insurancenum2
    },
    getThreshold2 (state, payload) {
      state.threshold2 = payload.thresholdnum2
    },
    getBnous (state, payload) {
      state.bnous = payload.bnousnum
    },
    getIncome (state, payload) {
      state.income = payload.incomenum
    },
    getBusiness (state, payload) {
      state.business = payload.businessnum
    },
    getPublicincome (state, payload) {
      state.publicincome = payload.publicincomenum
    },
    getMonth (state, payload) {
      state.month = payload.monthnum
    },
    getRemuneration (state, payload) {
      state.remuneration = payload.remunerationnum
    },
    getFranchise (state, payload) {
      state.franchise = payload.franchisenum
    },
    getLeased (state, payload) {
      state.leased = payload.leasednum
    },
    getTransferof (state, payload) {
      state.transferof = payload.transferofnum
    },
    getInterest (state, payload) {
      state.interest = payload.interestnum
    },
    getByaccident (state, payload) {
      state.byaccident = payload.byaccidentnum
    }
  }
})



