<template>
  <div class="calculator-2">
    <div class="pre-tax">
      <span class="input-name">税后工资（元）</span>
      <input type="text" class="input-number" v-model.number="wage2">
      <img src="../../../assets/images/circle.png" alt="" class="bordercircle">
    </div>
    <div class="social-insurance">
      <span class="input-name">各项社会保险（元）</span>
      <input type="text" class="input-number" v-model.number="insurance2">
      <img src="../../../assets/images/circle.png" alt="" class="bordercircle">
    </div>
    <div class="threshold">
      <span class="input-name">起征点（元）</span>
      <input type="text" class="input-number" v-model.number="threshold2">
      <img src="../../../assets/images/circle.png" alt="" class="bordercircle">
    </div>
  </div>
</template>

<script>
export default {
  name: 'ResultView',
  computed: {
    wage2: {
      get () {
        return this.$store.state.wage2
      },
      set (value) {
        this.$store.commit('getWage2', {
          wagenum2: value
        })
      }
    },
    insurance2: {
      get () {
        return this.$store.state.insurance2
      },
      set (value) {
        this.$store.commit('getInsurance2', {
          insurancenum2: value
        })
      }
    },
    threshold2: {
      get () {
        return this.$store.state.threshold2
      },
      set (value) {
        this.$store.commit('getThreshold2', {
          thresholdnum2: value
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/form.styl'
  .calculator-2
    .pre-tax
    .social-insurance
    .threshold
      form-style()
      .input-name
        form-text()
        margin-left .4rem
      .bordercircle
        border-circle()
      .input-number
        input-number()
        font-size .36rem
        color #88EAFF
    .social-insurance
      .input-number
        width 1.8rem
    .threshold
      .input-number
        width 2.85rem
</style>



