<template>
  <div class="form">
    <div class="income-type" @click.stop="showOption($event)">
      <span class="text">{{incomeType}}</span>
      <img src="../../../assets/images/Triangle.png" alt="" class="triangle">
      <img src="../../../assets/images/circle.png" alt="" class="bordercircle">
    </div>
    <transition name="expand">
      <ul class="income-option" v-if="optionState" @click="chooseHandle($event)">
        <li class="option" v-for="item of optionName" :key="item.index">{{item.text}}</li>
      </ul>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'HomeForm',
  data () {
    return {
      optionState: false,
      incomeType: '收入类型',
      optionName: [
        {text: '工资、薪金所得（月薪）'},
        {text: '工资、薪金所得（月薪 税后）'},
        {text: '年终奖所得'},
        {text: '劳务报酬所得'},
        {text: '个体工商户生产、经营所得'},
        {text: '对企事业单位的承包、租经营所得'},
        {text: '稿酬所得'},
        {text: '特许权使用费所得'},
        {text: '财产租赁所得'},
        {text: '财产转让所得'},
        {text: '利息、股息红利所得'},
        {text: '偶然所得'}
      ]
    }
  },
  methods: {
    showOption: function (event) {
      if (this.optionState === false) {
        this.optionState = true
      } else {
        this.optionState = false
      }
    },
    chooseHandle: function (event) {
      let target = event.target
      let li = document.querySelectorAll('li')
      for (let i in li) {
        if (target === li[i]) {
          let liIndex = parseInt(i)
          this.incomeType = this.optionName[liIndex].text
          this.optionState = false
          this.$emit('optionItem', liIndex)
          this.$store.commit('getformIndex', {
            formIndex: liIndex
          })
        }
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/form.styl'
  .form
    .income-type
      form-style()
      .text
        form-text()
      .triangle
        width .6rem
        height .58rem
        position absolute
        top .66rem
        left 5.54rem
      .bordercircle
        border-circle()
    .income-option
      position absolute
      left 50%
      top 3.7rem
      width 6rem
      margin-left -3rem
      z-index 10
      text-align center
      .option
        height .6rem
        line-height .6rem
        background-color rgba(0,73,166,.8)
        color #88EAFF
        border 1px solid #00FFFF
        margin-bottom .04rem
        border-radius 5px
    .expand-enter-active, .expand-leave-active
      transition: all .4s ease
    .expand-enter, .expand-leave-active
      transform: translate3d(0, -0.6rem, 0)
      opacity: 0
</style>



