<template>
  <div class="container">
    <result-header></result-header>
    <component :is="getshowWhatR()"></component>
    <result-footer></result-footer>
  </div>
</template>

<script>
import ResultHeader from './components/Header'
import ResultView1 from './components/PretaxR'
import ResultView2 from './components/AftertaxR'
import ResultView3 from './components/YearendbonusR'
import ResultView4 from './components/PersonalServiceR'
import ResultView5 from './components/BusinessR'
import ResultView6 from './components/PublicInstitutionR'
import ResultView7 from './components/RemunerationR'
import ResultView8 from './components/FranchiseR'
import ResultView9 from './components/LeasedPropertyR'
import ResultView10 from './components/TransferOfPropertyR'
import ResultView11 from './components/InterestR'
import ResultView12 from './components/ByAccidentR'
import ResultFooter from './components/Footer'
export default {
  name: 'Result',
  components: {
    ResultHeader,
    ResultView1,
    ResultView2,
    ResultView3,
    ResultView4,
    ResultView5,
    ResultView6,
    ResultView7,
    ResultView8,
    ResultView9,
    ResultView10,
    ResultView11,
    ResultView12,
    ResultFooter
  },
  data () {
    return {
      showWhatR: 'ResultView1'
    }
  },
  methods: {
    getshowWhatR: function () {
      let liIndex = this.$route.query.to
      let optionsIndex = parseInt(liIndex) + 1
      if (!optionsIndex) return
      this.showWhatR = 'ResultView' + optionsIndex
      console.log(this.showWhatR)
      return this.showWhatR
    }
  }
}
</script>

<style lang='stylus' scoped>
.container {
  width: 100%
  position: relative
  background: url('../../assets/images/bg.png') no-repeat center center
  background-size: cover
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center
  overflow-x: hidden
}
</style>



